/*
 * Shadow
 ** $sd- 로 시작
*/
$sd-black: #0000002c;

/*
 * background-color
 ** $bg- 로 시작
*/

$bg-white: #ffffff;
$bg-Main-color: #071b50;
$bg-writeback: #fbfcff;
$bg-line: #e2e2e2;
$bg-blue: #1c32c5;
$bg-blue-100: #eff1fc;
$bg-light-bule: #465ae1;
$bg-blue-50: #f6f6fc;
$bg-gray-50: #f6f6f6;
$bg-gray-100: #ececec;
$bg-blue-150: #0e1f91;
$bg-white-100: #f8f8ff;
$bg-gray: #7d7d7d;
$bg-gray-700: #afafaf;
$bg-gray-10: #f6f6f641;
$bg-green-100: #2a7e22;
$bg-green-200: #1d6117;
$bg-yellow-100: #dfc11a;
$bg-yellow-200: #cfb001;

/*
 * font
 ** $ft- 로 시작
*/

$ft-white: #ffffff;
$ft-Main-color: #071b50;
$ft-gray: #7d7d7d;
$ft-gray-100: #a7a7a7;
$ft-darkgray: #545454;
$ft-blue: #1c32c5;
$ft-line: #e2e2e2;
$ft-darkblue: #0b1b4c;
$ft-blue-100: #0e1f91;
$ft-skyblue: #808de5;
$ft-darklightblue: #212f6e;

/*
 * border
 ** $bd- 로 시작
*/

$bd-white: #ffffff;
$bd-line: #e2e2e2;
$bd-blue: #1c32c5;
$bd-skyblue: #808de5;
$bd-gray: #7d7d7d;
$bd-darkblue: #0b1b4c;
$bd-blue-100: #0e1f91;
$bd-gray-700: #afafaf;
$bd-gray-900: #9b9b9b;
$bd-white-80: #f5f5f5;
$bd-white-100: #f8f8ff;
$bd-gray-50: #f6f6f6;

.table {
  display: flex;
  flex-direction: row;
  padding: 2rem;
  min-width: 1440px;
  align-content: flex-start;
  font-size: 1rem;
  div {
    width: 100%;
  }
  &-list {
    width: 100%;
    text-align: center;
    border-collapse: collapse;
    &-title {
      font-size: 1rem;
      margin-bottom: 1rem;
      font-weight: 500;
      background-color: $bg-gray-50;
    }
    tr,
    td,
    th {
      border: 1px solid $bd-line;
      height: 2.5rem;
    }
  }
}

._button {
  padding: 0.3rem 0.8rem;
  width: 5rem;
  border: none;
  border-radius: 2px;
  color: $ft-white;
  font-weight: 600;
  transition: 0.5s;
  &:focus {
    outline: none;
  }
}

.true {
  background-color: $bg-green-100;

  &:hover {
    background-color: $bg-green-200;
  }
}

.false {
  background-color: $bg-yellow-100;
  &:hover {
    background-color: $bg-yellow-200;
  }
}
